<template>
    <div id="city">
        <!-- <header>
           <div class="to1">ele.me</div>
           <div class="to2"><i class="el-icon-user"></i></div>
        </header> -->
        <Header :Flagadd=false :FlagHome=false :FlagCity=true :FlagSignin=false :FlagSearch=false :FlagOrder=false :FlagMy=false :FlagAcc=false />
        <div class="content">
            <div class="con">
                <div class="con1">
                    <span>当前定位城市：</span>
                    <span>定位不准时，请在城市列表中选择</span>
                </div>
                <div class="con2">
                    <span>北京</span>
                    <router-link tag='span' to='/Address'>
                        <i class="el-icon-arrow-right"></i>
                    </router-link>
                </div>
            </div>
            <div class="cont">
                <div class="cont1">热门城市</div>
                <div class="cont2">
                    <div class="cont2-1" v-for="(a,index) in arr" :key="index" @click="add(a)">
                        <div  class="cont2-2">{{a.name}}</div>
                    </div>
                    
                </div>
            </div>
            <div class="contt" v-for="(item,index) in listner" :key="index">
                <div class="contt1">{{item}}(按字母排序)</div>
                <div class="contt2">
                <div class="contt2-1" v-for="(i,index) in list[item]" :key="index" @click="add(i)">
                    <div class="contt2-2">{{i.name | ellipsis}}</div>
                </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import Header from '../components/Header'
export default {
    components:{
     Header
    },
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
      list:[],
      listner:[],
      arr:[]
    }
  },
  filters: {
    ellipsis (value) {
        if (!value) return ''
        if (value.length > 5) {
            return value.slice(0,5) + '...'
        }
        return value
        }
  },
  created(){

  },
  methods:{
      add(s){
          this.$router.push(`/Address?id=${s.id}&name=${s.name}`)
      }
  },
  mounted(){
   
    this.axios.get("http://elm.cangdu.org/v1/cities?type=hot").then(res=>{
        this.arr=res.data
        console.log(res.data)
    })
    this.axios.get("http://elm.cangdu.org/v1/cities?type=group").then(res=>{
       console.log(res.data)
       this.list=res.data
       this.listner=Object.keys(res.data).sort()
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#city{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F5F5F5;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    display: flex;
        // header{
        //     width: 100%;
        //     line-height: 2rem;
        //     background: #3792E5;
        //     position: absolute;
        //     top: 0;
        //     z-index: 5;
        //     display: flex;
        //     .to1{
        //         margin-left: 0.5rem;
        //         color: white;
        //     }
        //     .to2{
        //         color: white;
        //         margin-left: 12rem;
        //     }
        // }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        // margin-top:2rem;
        .con{
            width: 100%;
            height: 4rem;
            background: white;
            .con1{
                line-height: 2rem;
                border-bottom: 0.01rem solid gainsboro;
                font-size: 0.5rem;
                span:nth-of-type(1){
                    margin-left: 0.4rem;
                }
                span:nth-of-type(2){
                    margin-left: 21%;
                    color: #9D9D9D;
                }
            }
            .con2{
                line-height: 2rem;
                border-bottom: 0.01rem solid gainsboro;
                span:nth-of-type(1){
                    margin-left: 0.4rem;
                    color: #72ADE7;
                }
                span:nth-of-type(2){
                    margin-left: 80%;
                    color: #9D9D9D;
                }
            }
        }
        .cont{
            width: 100%;
            background: white;
            margin-top: 1rem;
            .cont1{
                line-height: 2rem;
                margin-left:0.5rem;
            }
            .cont2{
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                .cont2-1{
                    width: 24.8%;
                    text-align: center;
                    line-height: 1.7rem;
                    border: 0.01rem solid gainsboro;
                    color: #72ADE7;
                }
            }
        }
        .contt{
            width: 100%;
            background: white;
            margin-top: 0.6rem;
            .contt1{
                color: gray;
                line-height: 2rem;
                margin-left:0.5rem;
            }
            .contt2{
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                .contt2-1{
                    width: 24.8%;
                    text-align: center;
                    line-height: 1.7rem;
                    border: 0.01rem solid gainsboro;
                }
            }
        }
    }
    
}
    
</style>



